body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background: hsla(294, 61%, 9%, 1);
}

ol, ul {
    padding-left: 30px;
}

.site-wrapper{
    background-color: hsla(0, 0%, 3%, 0.5);
}

.site-header{
    cursor: pointer;
    text-align: center;
    display: block;
    max-width: 30%; /*actual image width*/
    height: auto; /* maintain aspect ratio*/
    margin: auto;
}

.details-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: left;
            justify-content: left;
    color: hsla(294, 34%, 32%, 1);
}

.details-headers {
    color: hsla(360, 100%, 100%, 1);
}

.details-row-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: left;
            justify-content: left;
}

.poster-wrapper > img {
    width: 15em;
    padding: 5px;
}

.show-title {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: left;
            justify-content: left;
}

.show-basic-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: left;
            justify-content: left;
}

.episodes-wrapper {
    min-width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.season-wrapper-focused {
    font-weight: bold;
    color: hsla(40, 98%, 60%, 1);
    margin: 5px;
    cursor: pointer;
}

.season-wrapper {
    margin: 5px;
    cursor: pointer;
}

.catalogue-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

.show-title-catalogue  {
    color: hsla(40, 98%, 60%, 1);
    text-align: center;
}

.show-wrapper  {
    -webkit-flex: 0 1 auto;
            flex: 0 1 auto;
    padding: 10px;
}

.show-wrapper > img {
    cursor: pointer;
    display: block;
    max-width: 70%; /*actual image width*/
    height: auto; /* maintain aspect ratio*/
    margin: auto; /*optional centering of image*/
}
.wrapper {
}

.header{
    cursor: pointer;
    border: solid 1px hsla(294, 34%, 32%, 1);
    padding: 15px;
    background-color: hsla(294, 61%, 21%, 0.5);
    color: hsla(40, 98%, 60%, 1);
    font-family: verdana;
}

.content{
    /*cursor: pointer;*/
    border-left: solid 1px hsla(0, 0%, 7%, 1);
    border-right: solid 1px hsla(0, 0%, 7%, 1);
    border-bottom: solid 1px hsla(0, 0%, 7%, 1);
    border-radius: 0 0 5px 5px;
    padding: 15px;
    font-family: verdana;
    font-size: 14px;
    color: hsla(294, 29%, 38%, 1);
}
